		},
		methods: {
			login() {
				uni.showLoading({
					title: '登入中',
					mask: true
				});

				var toKen
				uni.request({
					url: baseUrl + 'sys/login',
					// url: baseUrl + 'sys/loginApp',
					method: 'POST',
					data: {
						'username': this.username,
						'password': this.password
					},
					header: {
						'X-Requested-With': 'XMLHttpRequest',
						"Content-Type": "application/json; charset=UTF-8",
						'toKen': toKen,
					},
					success: (res) => {
						if (res.data.code === 5001) {
							uni.showToast({
								title: res.data.msg,
								duration: 2000,
								icon: 'none',
							});
						}
						if (res.data.code === 0) {
							uni.hideLoading(); //隐藏 loading 提示框。
							toKen = res.data.data.session_id
							uni.setStorage({
								key: 'toKen',
								data: toKen

							})
							uni.showToast({
								title: '登入成功',
								duration: 2000,
							});

							uni.setStorage({
								key: 'tabbarList',
								data: this.list

							})
							uni.setStorage({
								key: 'index',
								data: 0

							})

							uni.reLaunch({
								url: this.list[0].pagePath
							})

							// 委托机构
							// uni.reLaunch({
							// 	url: '../entrustedAgency/upcoming/upcoming'
							// })
