				});
			},
			SignIn() {
				if (this.username === '') {
					uni.showToast({
						title: '请输入账号',
						duration: 2000,
						icon: 'none',
					});
				} else if (this.password === '') {
					uni.showToast({
						title: '请输入密码',
						duration: 2000,
						icon: 'none',
					});
				} else {
					this.login()


					// login({
					// 	username: this.username,
					// 	password: this.password
					// }).then(res => {
					// 	if (res[1].data.code === 0) {
					// 		uni.setStorage({
					// 			key: 'toKen',
					// 			data: res[1].data.data.session_id

					// 		})
					// 		uni.showToast({
					// 			title: '登入成功',
					// 			duration: 2000,
					// 		});

					// 		uni.setStorage({
					// 			key: 'tabbarList',
					// 			data: this.list

					// 		})
					// 		uni.setStorage({
					// 			key: 'index',
					// 			data: 0

					// 		})

					// 		uni.reLaunch({
					// 			url: '../serviceProvider/requisitionData/requisitionData'
					// 		})
					// 	}
					// })


				}

			}
