	}
</script>

<style scoped>
	.login_box {
		height: 100vh;
		padding: 35% 0;
		background-color: #323542;
		box-sizing: border-box;
	}

	.title {
		width: 100%;
		color: #fff;
		text-align: center;
		position: absolute;
		top: 10%;
		font-size: 50rpx;
	}

	.image {
		text-align: center;
	}

	.image image {
		width: 200px;
		height: 72px;
	}

	.input_box {
		padding: 100rpx;
		box-sizing: border-box;
	}

	.bottom {}

	.input_box input {
		font-size: 24rpx;
		padding-left: 80rpx;
		padding-right: 60rpx;
		background-color: #464955;
		margin-bottom: 50rpx;
		height: 100rpx;
		line-height: 100rpx;
		border-radius: 5px;
		color: #a9a9a9;
		position: relative;
	}

	.input_box button {
		line-height: 100rpx;
		background-color: #0bd38a;
		color: #fff;
	}

	.password_Input {
		position: relative;
	}

	#icon {
		text-align: center;
		width: 50rpx;
		height: 50rpx;
		line-height: 50rpx;
		font-size: 24rpx;
		position: absolute;
		bottom: 5rpx;
		right: 5rpx;
		z-index: 2;
	}
</style>
